import { gql } from '../../__generated__/gql';

// =============================== REPORTS ==================================

export const GET_REPORTS = gql(`
    query GetReports {
        getReports {
            id
            name
            basis
            frequency
            localDue
            nationalDue
        }
    }
`)

export const GET_REPORT_BY_ID = gql(`
    query GetReportById($id: Int!) {
        getReportById(id: $id) {
            id
            name
            basis
            frequency
            localDue
            nationalDue  
        }
    }
`)

export const GET_REPORT_SUMMARY = gql(`
    query GetReportSummary {
        getReportSummary {
            office
            pending
            submitted
            total
        }
    }
`)

export const GET_REPORT_STATISTICS = gql(`
    query GetReportStatistics($officeId: Int) {
        getReportStatistics(officeId: $officeId) {
            overdue
            pending
            submitted
            total
        }
    }
`)

export const CREATE_REPORT = gql(`
    mutation CreateReport($name: String!, $basis: String!, $localDue: String!, $nationalDue: String!, $frequency: Frequency!) {
        createReport(name: $name, basis: $basis, localDue: $localDue, nationalDue: $nationalDue, frequency: $frequency) {
            id
        }
    }
`)

export const UPDATE_REPORT = gql(`
    mutation UpdateReport($id: Int!, $name: String!, $basis: String!, $localDue: String!, $nationalDue: String!, $frequency: Frequency!) {
        updateReport(id: $id, name: $name, basis: $basis, localDue: $localDue, nationalDue: $nationalDue, frequency: $frequency) {
            id
        }
    }
`)

export const DELETE_REPORT = gql(`
    mutation DeleteReport($id: Int!) {
        deleteReport(id: $id) {
            id
        }
    }
`)

// =============================== SUBMISSION ==================================

export const GET_SUBMISSIONS = gql(`
    query GetSubmittedReports($officeId: Int) {
        getSubmittedReports(officeId: $officeId) {
            id
            office {
                id
                name
            }
            report {
                id
                name
                basis
            }
            status
            localDue
            nationalDue
        }
    }
`)

export const GET_SUBMISSION_BY_ID = gql(`
    query GetSubmittedReportById($id: Int!) {
        getSubmittedReportById(id: $id) {
            id
            office {
                id
                name
            }
            report {
                id
                name
                basis
            }
            status
            localDue
            nationalDue
            message
            files
        }
    }
`)

export const SUBMIT_REPORT = gql(`
    mutation SubmitReport($id: Int!, $message: String, $files: [String!]) {
        submitReport(id: $id, message: $message, files: $files) {
            id
        }
    }
`)